  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #000000;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

.contenido-centrado {
  max-width: 800px; }

.no-margin {
  margin: 0; }

.boton {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  cursor: pointer; }
  .boton:hover {
    background-color: #619800; }

.no-webp .inicio {
  background-image: url("../img/fondo2.jpg"); }

.webp .inicio {
  background-image: url("../img/fondo2.webp"); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .contenido-header {
        min-height: 60rem; } }
    .header.inicio .contenido-header h1 {
      text-align: left;
      color: #FFFFFF;
      font-weight: 700;
      margin-top: 10rem;
      font-size: 6rem; }
    .header.inicio .contenido-header p {
      color: #FFFFFF; }

.barra {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
  align-items: center; }
  .barra .logo {
    font-size: 3rem;
    color: #FFFFFF; }

.navegacion a {
  color: #FFFFFF;
  font-weight: 700;
  margin-right: 1.5rem; }
  .navegacion a:last-of-type {
    margin-right: 0; }
  .navegacion a:hover {
    color: #94d12a; }

.contenedor-boton {
  width: 60%; }
  .contenedor-boton p {
    font-size: 2.2rem; }
